* {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: Helvetica, Arial, sans-serif;
  font-size: $m-size;
}

button {
  border: none;
  border-radius: 0;
  cursor: pointer;
}

button:disabled {
  cursor: default;
}

input {
  border: none;
}



